/*BANNER CSS START*/
.banner{position: relative;width: 100%;padding-top: 90px;min-height: 150px;}
.banner figure {float: left;width: 100%;position: absolute;top: 0;left: 0;bottom: 0;right: 0;margin: auto;width: 100%;height: 100%;}
.banner figure img {width: 100%;height: 100%;object-fit: cover;}
.bannerContent{width: 100%;position: relative;text-align: start;padding: 100px 0;}
.bannerContent h1{font-size: 40px;padding-bottom: 100px;}
.bannerContent h1::before{display: none;}
/*BANNER CSS END*/

/* CONTACT PAGE CSS START */
.contactArea{padding: 100px 0;}
.contactInfo li i{font-size: 20px;line-height: 21px;}
.contactInfo li{float: left;width: 100%;margin-bottom: 10px;}
.contactInfo li:last-child{margin-bottom: 0;}
.contactInfo li address,.contactInfo li .contactNumber{padding-left: 12px;width: calc(100% - 20px);display: inline-block;vertical-align: middle;font-size: 16px;line-height: 21px;font-weight: 300;font-family: 'Signika Negative', sans-serif;}
.contactNumber a{float: left;width: 100%;color: #002347;font-size: 16px;line-height: 20px; text-decoration: none;}
.contactNumber span{float: left;width: 100%;color: #002347;font-size: 14px;line-height: 20px;font-weight: 300;font-family: 'Signika Negative', sans-serif;font-size: 13px;line-height: 15px;}
.contactPageForm .formControl{border-radius: 0;height: 37px;font-weight: 300;font-family: 'Signika Negative', sans-serif;font-size: 13px;line-height: 15px;}
.inputField .formGroup:last-child{margin-bottom: 0;}
.contactPageForm textarea.formControl{height: 132px;font-weight: 300;font-family: 'Signika Negative', sans-serif;}
.contactSubmitBtn .btn{float: right;width: auto;padding: 14px 50px;background-color: #252a65;font-size: 16px;line-height: 17px;color: #fff;margin-top: 20px;border-radius: 0;border: none;font-weight: 600;font-family: 'Signika Negative', sans-serif;}
/* CONTACT PAGE CSS END */