
.loginForm{box-sizing: border-box; position: relative; min-height: 90vh; background:#f5f5f5; display: flex; justify-content: center;align-items: center;padding: 20px; padding-top: 100px; padding-bottom: 35px; font-family: "Signika Negative", sans-serif;}
.loginForm .Container {position: relative; width: 70%; height: 500px; background: #fff;box-shadow: 0 15px 50px rgba(0,0,0,0.1);overflow: hidden;}
.addform{padding-top: 100px;padding-bottom: 25px;}
.loginForm .Container .user {position: absolute;top:0;left: 0;width: 100%;height: 100%;display: flex;}
.loginForm .Container .user .imgBx{position: relative;width: 50%;height: 100%;background:#252a65;transition: 0.5s;}
.loginForm .Container .user .imgBx img {position:absolute;top: 0;left: 0;width: 100%;height: 100%;object-fit: cover;}
.loginForm .Container .user .formBx {position: relative;width: 50%;height: 100%;background: #fff;display: flex;justify-content: center;align-items: center;padding: 40px;transition: 0.5s;}
.loginForm .Container .user .ufrom {position: relative;width: 50%;height: 100%;background: #fff;display: flex;justify-content: center;align-items: center;padding: 0 40px;transition: 0.5s;}
.loginForm .Container .user .formBx h2 {font-weight: 700;text-transform: uppercase;letter-spacing: 2px;text-align: center;width: 100%;}
.loginForm .Container .user .delh2 h2::before {left: 0px;top: -8px;}
.loginForm .Container .user .delh2 h2{font-size: 30px;text-align: start;}
.loginForm .Container .user .addemp h2::before{top: 25px; left: 130px;}
.loginForm .Container .user .addemp h2 {font-size: 30px; padding: 30px 0; text-align: center;}
.loginForm .Container .user .log h2::before {left:0px;top: -10px;}
.loginForm .Container .user .getemp h2{font-size: 30px;}
.loginForm .Container .user .ugetemp h2{font-size: 30px;}
.loginForm .Container .user .udata h2{font-size: 30px; padding: 50px 0; text-align: center;}
.loginForm .Container .user .udata h2::before{left: 120px;top: 40px;}
.loginForm .Container .user .ugetemp h2::before{left: 10px; top: -10px;}
.loginForm .Container .user .log h2{font-size: 30px;text-align: start;}
.loginForm .Container .user .getemp h2::before {left:10px;top: -10px;}

.loginForm .Container .user .formBx form input{position: relative;width: 100%;padding: 10px; background: #f5f5f5;color:rgb(24, 23, 23);border: none;outline: none;box-shadow: none;margin: 8px 0;font-size: none;margin: 8px 0;font-size: 14px;letter-spacing: 1px;font-weight: 500;}
.loginForm .Container .user .formBx form input[type='submit']{max-width: 100px; background: #252a65;color: #fff;cursor: pointer;font-size: 14px; font-weight: 500px;letter-spacing: 1px; transition: 0.5s;}
.loginForm .Container .user .formBx form .Login a{font-size: 14px; font-weight: 600; text-decoration: none;color: #252a65; letter-spacing: 1px;text-transform: uppercase;}
.loginForm .Container.active .signupBx {pointer-events: initial;}
.loginForm .Container .signupBx {pointer-events: none;}
.loginForm .Container .signupBx .formBx{left:100%;}
.loginForm .Container .signupBx .imgBx{left:-100%;}
.loginForm .Container.active .signupBx .formBx {left:0}
.loginForm .Container.active .signupBx .imgBx{left:0;}
.loginForm .Container .signinBx .formBx{left: 0;}
.loginForm .Container .signinBx .imgBx{left:0;}
.loginForm .Container.active .signinBx .formBx {left:100%}
.loginForm .Container.active .signinBx .imgBx{left:-100%;}
.loginForm .upfrom {height: 600px;}
.loginForm .Container .user .ufrom form input{position: relative;width: 70%;padding: 8px 5px; background: #f5f5f5;color:rgb(24, 23, 23);border: none;outline: none;box-shadow: none;margin: 10px 0;font-size: none;font-size: 14px;letter-spacing: 1px;font-weight: 500; float: right;}
.loginForm .Container .user .ufrom form label {float: left; line-height: 4; width: 25%;}
.loginForm .Container .user .ufrom form input[type='submit']{max-width: 130px; background: #252a65;color: #fff;cursor: pointer;font-size: 14px; font-weight: 500px;letter-spacing: 1px; transition: 0.5s; float: left;}


@media only screen and (max-width: 767px){  
    
    .data p {font-size: 12px;}
    .loginForm .Container .user .ugetemp h2{font-size: 13px; float: left;text-align: left;}
    .loginForm .Container .user .ugetemp h2::before{left: 0px; width: 50px;}
    .Login {font-size: 13px;}
    
    .loginForm .Container{
        max-width: 400px;
    }
    .loginForm .Container .user .formBx{padding: 10px;}
    .loginForm .Container .imgBx{
        display: none;
    }
    .loginForm .Container .user .formBx{
        width: 100%;
    }
    .loginForm .Container{width: 100%;}
    .loginForm .Container .user .log h2{text-align: left; float: left; font-size: 19px;}
    .loginForm .Container .user .log h2::before{width: 30px; left: 0;}
}

@media only screen and (max-width:1199px){
    .loginForm .Container .user .addemp h2::before{top: 20px; left: 65px;}
    .loginForm .Container .user .delh2 h2{font-size: 25px;text-align: left;}
    .loginForm .Container .user .getemp h2{font-size: 18px; float: left; text-align: left;}
    .loginForm .Container .user .ufrom form label {display: none}
    .loginForm .Container .user .ufrom form input {width: 100%;}
    .loginForm .Container .user .ugetemp h2{font-size: 25px;text-align: left;}
}