/*BANNER CSS START*/
.banner{position: relative;width: 100%;padding-top: 100px;min-height: 150px;padding-bottom: 50px;}
.banner figure {float: left;width: 100%;position: absolute;top: 0;left: 0;bottom: 0;right: 0;margin: auto;width: 100%;height: 100%;}
.banner figure img {width: 100%;height: 100%;object-fit: cover;}
.bannerContent{width: 100%;position: relative;text-align: center;padding: 120px 0;}
.bannerContent h1{font-size: 40px;}
.bannerContent h1::before{display: none;}
/*BANNER CSS END*/

.ideaimage figure{width: 400px;float: left;height: 350px;position: relative;}
.ideaimage figure img{float: left;width: 100%;height: 100%;position: relative;z-index: 1;object-fit: cover;}
.ideaimage figure::after{content: '';position: absolute;border: 10px solid #5f67c7;display: inline-block;width: 100%;height: 100%;right: -25px;top: -25px;}
.ideaimage{margin-top: 27px;}
.ideacontent h2 .span1{float: left;width: 100%;font-weight: 900;font-size: 70px;line-height: 90px;}
.ideacontent h2 .span2{float: left;width: 100%;font-weight: 900;font-size: 30px;line-height: 50px;}
.ideacontent{padding: 119px 0;}
/* service css start */
.pointscivil,.points {padding: 40px 0;}
.servicehead h3::before {left: 435px; top: -8px;}
.servicehead h3 {margin-top: 30px;}
.steelservice h4{ font-size: 25px; font-weight: 600; color: #252a65; letter-spacing: 1px; margin-bottom: 10px;margin-top: 10px;}
.steelservice p{font-size: 14px;font-weight: 400; color: #000;}
.steelservice p span {font-size: 17px;color: #252a65;font-weight: 700;text-transform: uppercase;}
.steelservice ul li {font-size: 14px; font-weight: 400;margin:0px}
.consultinghead h3{margin-top: 30px;font-size: 35px;}
.points,.pointscivil p {font-size: 15px;}
.points p span ,.pointscivil p span {text-transform: uppercase;color: #252a65;font-size: 17px;font-weight: 700;}
.steelservice figure img {border-radius: 20%;padding: 50px 0;}
/* service css end */

/* responsive  css start*/
@media only screen and (max-width: 1199px){.servicehead h3::before{left: 333px;}}
@media only screen and (max-width: 991px){.servicehead h3::before{left: 228px;}.consultinghead h3::before{left:300px;top:-7px}.steelservice p{font-size: 12px;font-weight: 400; color: #000;}.pointscivil p {font-size: 12px;}.steelservice ul li{font-size: 12px;}.servicehead h3{font-size: 25px;}.steelservice h4{font-size: 18px;margin-bottom: 10px;}}
@media only screen and (max-width: 767px){.servicehead h3::before{left: 0px;} .servicehead h3{text-align: start;}.consultinghead h3::before{left:0px;top:-7px}.consultinghead h3{text-align: start; margin-bottom: 20px;}
.ideaimage {padding-bottom: 20px;}
.points p {font-size: 14px; padding-top: 10px;}
.ideaimage figure{width: 100%;float: left;height: 350px;position: relative;}
.ideaimage figure img{float: left;width: 100%;height: 100%;position: relative;z-index: 1;object-fit: cover;}
.ideaimage figure::after{content: '';position: absolute;border: 10px solid #5f67c7;display: inline-block;width: 100%;height: 100%;right: -10px;top: -10px;}
.ideaimage{margin-top: 27px;}
.ideacontent h2 .span1{float: left;width: 100%;font-weight: 900;font-size: 45px;line-height: 55px;}
.ideacontent h2 .span2{float: left;width: 100%;font-weight: 900;font-size: 23px;line-height: 30px;}
.ideacontent{padding: 30px 0;}}
/* responsive css end */