/*BANNER CSS START*/
.banner{position: relative;width: 100%;padding-top: 90px;min-height: 150px;}
.banner figure {float: left;width: 100%;position: absolute;top: 0;left: 0;bottom: 0;right: 0;margin: auto;width: 100%;height: 100%;}
.banner figure img {width: 100%;height: 100%;object-fit: cover;}
.bannerContent{width: 100%;position: relative;text-align: center;padding: 90px 0;}
.bannerContent h1 {font-size: 40px;padding-bottom: 100px;}
.bannerContent h1::before{display: none;}
/*BANNER CSS END*/

/*client service start*/
.sectionpadding{padding: 90px 0;}
.ourclientheading h3{font-size: 35px;}
.ourclientheading h3::before{left: 60px;width: 80px; top: -8px;}
.clientService .serviceImage figure{float: left;width: 100%;height: 200px;padding: 15px;}
.clientService .serviceImage figure img{width: 100%;height: 100%;}
.clientService{margin-bottom: 20px;}
.showNow span {color: #252a65;}
.showNow{font-size: 19px; position: absolute;left: 52%;background-color: #fff;width: 80px;height: 40px;text-align: center;display: flex;flex-wrap: wrap;justify-content: center;align-items: center;top: 40%;border-radius: 5%;opacity: 0;transform: translate(-50%, -50%);text-decoration: none;}
.show{font-size: 20px;font-weight: 600; color:#252a65;opacity: 0; text-align: left; padding: 0 10px;padding-bottom: 20px;text-transform: uppercase;}
.clientService .serviceImage figure:hover {opacity: 0.9;transition: 1s; transform: scale(1.1) translate3d(0, 0, 0);}
.clientService:hover .showNow {opacity: 1; transition: 1s;}
.domestic ,.inter {font-size: 25px;color: #252a65;font-weight: 700;padding: 20px;}
/*client service end*/


@media only screen and (max-width:991px){
    .bannerContent h1 {font-size: 25px;}
    .clientService{height: 230px;}
    .sectionpadding{padding: 50px 0;}
    .ourclientheading h3{text-align: start;font-size: 25px;}
    .ourclientheading h3::before{left: 0;width: 80px;}
    .show {opacity: 1; padding: 0 5px;padding-bottom: 20px;font-size: 15px;}
    .showNow{font-size: 16px; position: absolute;float: right;background-color: #252a65;width: 60px;height: 35px;text-align: center;display: flex;flex-wrap: wrap;justify-content: center;align-items:center;top: 82.6%;left: 80%; border-radius: 5%;opacity: 1;transform: translate(-50%, -50%);text-decoration: none;}
    .showNow span {color: #fff;text-decoration: none;}
}
@media only screen and (max-width:767px){
    .show {font-size: 9px;padding: 0 5px;padding-bottom: 20px;}
    .showNow{font-size: 9px; position: absolute;float: right;background-color: #252a65;width: 30px;height: 25px;text-align: center;display: flex;flex-wrap: wrap;justify-content: center;align-items:center;top: 85%;left: 77%; border-radius: 5%;opacity: 1;transform: translate(-50%, -50%);text-decoration: none;}
    
}