.footer{background-color: #002347;padding: 40px 0 0 0;}
.footer h4{color: #fff;margin-bottom: 35px;font-size: 25px;}
.footer h4::after{border-color: #fff; top:35px}
.footer ul{display: inline-block;vertical-align: middle;float: none;}
.footer ul li{display: inline-block;width: 100%;margin-bottom: 10px;}
.footer ul li a{width: 100%;}
.footer ul li a,.reach-us{color: #727b83;font-size: 13px;line-height: 15px;}
.footer ul li a:hover,.reach-us:hover{color: #fdc632; text-decoration: none;}
.reach-us address{width: calc(100% - 65px) !important;padding-left: 10px;display: inline-block;vertical-align: middle;}
.contact{width: calc(100% - 148px) !important;}
.social-icon a{margin-right: 50px;}
.social-icon {padding:20px 0;}
.social-icon a:last-child{margin-right: 0;}
.reach-us span{float: left;width: 100%;}
.reach-us{display: inline-block;vertical-align: middle;float: none;}
.email.contact{display: inline-block;width: auto !important;}
.reach-us:hover i,.reach-us:hover a{color: #fdc632;}
.reach-us:hover span{color: #7b838a;}
.reach-us a{padding-left: 10px;}
.copy-right{padding: 0 0 30px 0;text-align: center;background-color: #002347;}
.copy-right p{color: #fff;font-size: 15px;line-height: 20px;width: 100%;text-align: center;float: none;display: inline-block;}
.socila-links{margin-bottom: 0;}
.social-icon i{font-size: 20px;}
.reach-us i{display: inline-block;vertical-align: middle;line-height: 22px;font-size: 14px;line-height: 16px;}
.copyright{background-color: #fff;height: 30px;}
.copyright .text {float: left;font-size: 15px;padding: 15px 20px; text-decoration: none;font-weight: 600;}
.copyright a{cursor: pointer;color: rgb(42, 39, 39);}
.copyright a:hover {color: #002347;}


/* responsive */
@media only screen and (max-width: 1199px){
    .container > .row{margin: 0;}
  
}


@media only screen and (max-width: 767px){
  .copyright .text {text-align: center;padding: 5px 10px;font-size: 17px;}
    .footer-section .footer-content{margin-bottom: 20px;}
    .footer-section:last-child{margin-bottom: 0;}
}
