/*BANNER CSS START*/
.banner{position: relative;width: 100%;padding-top: 90px;min-height: 600px;}
.banner figure {float: left;width: 100%;position: absolute;top: 0px;left: 0;bottom: 0;right: 0;margin: auto;width: 100%;height: 100%;}
.banner figure img {width: 100%;height: 100%;object-fit:cover}
.bannerContent{width: 100%;position: relative;text-align: center;padding: 70px 0;}
.bannerContent p{font-size: 30px;line-height: 30px;color: #fff;margin-bottom: 14px;font-weight: 600;font-family: 'Signika Negative', sans-serif;}
.bannerContent h1::before{display: none;}
.bannerContent h1 {font-size: 50px; width: 100%;}
/*BANNER CSS END*/

/*services css start*/
.autowidth{width: 33.33%;float: left;max-width: 100%;}
.servicesimage{width: 100%;opacity: 0;}
.servicesection{margin-top: -253px;}
.servicesimage figure{float: left;width: 100%;height: 253px;}
.servicesimage figure img{float: left;width: 100%;height: 100%;}
.bordercontent{background-color: #fff;}
.bordercontent .border{border: 2px solid #000 !important;background-color: #000;width: 100%;float: left;height: 3px;}
.servicesection:hover .servicesimage{width: 100%;opacity: 1;}
.servicesection:hover{position: relative;z-index: 1;}
.servicesection:hover .bordercontent{margin-top: -20px;box-shadow: 0px 0px 18px rgba(0,0,0,0.5);transition: 1s;}
.boxicon{position: relative;top: -50px;right: 0;float: none;display: inline-block;left: 50%;transform: translateX(-50%);width: auto;}
.boxicon span{width: 100px;height: 100px;float: left;border-radius: 50%;background-color: #fff;position: relative;border: 1px solid transparent;}
.boxicon span i{color: #fff;display: inline-block;font-size: 25px;margin: 0 auto;line-height: 99px;position: absolute;right: 0;left: 0;text-align: center;}
.servicesection1 .bordercontent .border{background-color: #f4d03f;border-color: #f4d03f !important;}
.servicesection1 .boxicon span{border-color: #f4d03f;}
.servicesection1 .boxicon span i{color: #f4d03f;}
.servicesection2 .bordercontent .border{background-color: #2e8ece;border-color: #2e8ece !important;}
.servicesection2 .boxicon span{border-color: #2e8ece;}
.servicesection2 .boxicon span i{color: #2e8ece;}
.servicesection3 .bordercontent .border{background-color: #ec644b;border-color: #ec644b !important;}
.servicesection3 .boxicon span{border-color: #ec644b;}
.servicesection3 .boxicon span i{color: #ec644b;}
/*.main-service-section h2{width: auto;float: none;display: inline-block;}*/
.serviceimage figure{width: 360px;height: 200px;float: left;overflow: hidden; object-fit: cover;}
.serviceimage figure img{float: left;width: 100%;height: 100%;object-fit: cover;transform: scale(1) translate3d(0, 0, 0);transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);object-fit: cover;}
.servicedesc{background-color: #f9f9ff;padding: 30px 30px;}
.servicedesc a{background-color: #002347;color: #fff;padding: 10px 40px;border-radius: 0;border: none;font-size: 13px;line-height: 15px;font-weight: 300;font-family: 'Nunito', sans-serif;}
.servicedesc a:hover{background-color: #002347 !important;color: #fff !important;}
.homeservice:hover .serviceimage figure img{transform: scale(1.1) translate3d(0, 0, 0);}
.mainservicesection h3{width: 329px;}
/*services css end*/

/*IDEA CSS SATRT*/
.ideaimage figure{width: 400px;float: left;height: 350px;position: relative;}
.ideaimage figure img{float: left;width: 100%;height: 100%;position: relative;z-index: 1;object-fit: cover;}
.ideaimage figure::after{content: '';position: absolute;border: 10px solid #5f67c7;display: inline-block;width: 100%;height: 100%;right: -25px;top: -25px;}
.ideaimage{margin-top: 27px;}
.ideacontent h2 .span1{float: left;width: 100%;font-weight: 900;font-size: 70px;line-height: 90px;}
.ideacontent h2 .span2{float: left;width: 100%;font-weight: 900;font-size: 30px;line-height: 50px;}
.ideacontent{padding: 119px 0;}
/*IDEA CSS END*/

/*why steel solution css start*/
.whyusimage figure{position: relative;width: 400px;height: 350px;}
.whyusimage figure img{float: left;width: 100%;height: 100%;position: relative;z-index: 1;object-fit: cover;}
.whyusimage figure::before{content: '';position: absolute;width: 100%;height: 100%;border: 10px solid #5f67c7;display: inline-block;left: -25px;top: -25px;}
.whyusimage{margin-top: 27px;}
.whyuscontent p{margin-bottom: 15px;font-size: 16px;}
.whyuscontent h3{margin-bottom: 20px;font-size: 35px;}
.whyuscontent h3::before{top: -8px;}
.whyuscontent p:last-child{margin-bottom: 0;}
.whyuscontent{padding: 99px 0;}
/*why steel solution css end*/

/*prioriters css start*/

.ourspeciality h3{text-align: center; margin-bottom:20px;font-size: 35px;}
.ourspeciality h3::before {left: 470px;top:-6px;}
.infosection{padding: 20px;min-height: 485px;}
.priority p{margin-bottom: 20px;font-size: 15px;}
.infosection p span{font-weight: 900;color: #252a65;}
.infosection p{margin-bottom: 5px;font-size: 15px;}
.infosection h5 {margin-bottom: 0;}
.infosection p:last-child{margin-bottom: 0;}
.infosection h4{width: auto;float: none;display: inline-block;}
.infosection .maintitle{margin-bottom: 15px; text-align: center; justify-content: center;}
.priority{margin-bottom: 20px;}
.advantagesinfo:first-child .infosection{border-top: 3px solid #f4d03f;border-bottom: 3px solid #f4d03f;}
.advantagesinfo:nth-child(2n) .infosection{border-top: 3px solid #2e8ece;border-bottom: 3px solid #2e8ece;}
.advantagesinfo:last-child .infosection{border-top: 3px solid #ec644b;border-bottom: 3px solid #ec644b;}
/*priorities css end*/

/* CLIENT happy START */
.ourhappyclient ::before{left: 55px; top: -10px;}
.clientsection{padding: 0 0 100px 0;}
.clientinfo{padding: 50px 55px;}
.clientinfo{box-shadow: 0 0px 12px rgba(0,0,0,.3)}
.clientimage figure{width: 133px;height: 173px;float: left;}
.clientimage figure img{float: left;width: 100%;height: 100%;}
.clientdesc h4{margin-bottom: 20px;}
.clientdesc p{line-height: 25px;}
.ourhappyclient h3{width: 425px; margin:30px 0;}
/* CLIENT CSS END */

/*client service start*/
.subHeading h3{margin-bottom: 20px;font-size: 35px;}
.subHeading h3::before{left: 490px;top: -5px;}

.imageHover{float: left;width: 100%;position: relative; height: 260px;}
.imageHover figure{width: 100%;height: 200px;float: left; object-fit: cover;overflow: hidden;}
.imageWrap {margin: 10px 0;}
.imageHover figure img{width: 100%;height: 100%;float: left; overflow: hidden; object-fit: cover;}
.imageWrap .imageHover:hover figure img{opacity: 0.8; transition: 1s; transform: scale(1.1) translate3d(0, 0, 0);}
.imageHover .showNow{font-size: 19px; position: absolute;left: 52%;background-color: #fff;width: 80px;height: 40px;text-align: center;display: flex;flex-wrap: wrap;justify-content: center;align-items: center;top: 40%;border-radius: 5%;opacity: 0;transform: translate(-50%, -50%);text-decoration: none;}
.imageHover .show{font-size: 20px;font-weight: 600; color:#252a65;opacity: 1; text-align: center; margin-top: 20px;}
.imageWrap .imageHover:hover .showNow{opacity: 1; transition: 1s;}

/*client service end*/

/* Slider css start */
.galleryheading h2{font-size: 35px; margin: 20px 0;}
.galleryheading h2::before{left:530px; width: 60px;top: -8px;}
.clientheading h2{font-size: 35px;}
.clientheading h2::before{left:450px;top: -8px;}
.slider figure{width: 100%; height: 300px;}
.slider figure img{width: 100%; height: 300px; object-fit: cover;}
.sliderclient figure {height: 200px;}
.sliderclient figure img {height: 200px;padding: 30px;object-fit: contain;}
/* Slider css end */
@media only screen and (max-width: 767px){
    .imageHover .show{font-size: 12px;}
    
    .galleryheading h2{font-size: 35px; margin: 20px 0; text-align: start;}
    .galleryheading h2::before{left:0px; width: 90px;}
    .bannerContent h1 {font-size: 30px; width: 100%;}
    .navigationmenu{float: left;width: 100%;padding: 0 20px;}
    .navigationmenu li{margin-right: 0;}
    .servicesimage{opacity: 1;}
    .servicesection{margin-top: 0;}
    .servicesection.autowidth{width: 100%;margin-bottom: 30px;}
    .servicesstart{padding: 50px 0;}
    .servicesimage figure,.serviceimage figure,.clientimage figure{height: 250px;}
    .servicesection .bordercontent,.servicesimage{box-shadow: 0px 0px 18px rgba(0,0,0,0.5);}
    .whyusimage figure,.serviceimage figure,.clientimage figure{width: 100%;}
    .homeservicesection .homeservice{margin-bottom: 20px;}
    .homeservicesection:last-child .homeservice{margin-bottom: 0px;}
    .advantagesinfo .infosection{margin-bottom: 20px;}
    .advantagesinfo:last-child .infosection{margin-bottom: 0px;}
    h1{font-size: 50px;line-height: 50px;}
    .bannercontent{padding: 90px 0;}
    .banner{min-height: 460px;}
    .ideassection.sectionpadding{padding: 0 0 30px 0;}
    .servicesection.autowidth:last-child{margin-bottom: 0;}
    h2{font-size: 35px;line-height: 31px;}
    .ideacontent h2 .span1{font-size: 40px;line-height: 50px;}
    .ideacontent h2 .span2{font-size: 30px;line-height: 37px;}
    .ideacontent{padding: 0 0;}
    h2::before, h3::before{top: -10px;}
    h3{font-size: 30px;line-height: 35px;}
    .whyuscontent{padding: 60px 0 0 0;}
    .sectionpadding.mainservicesection .maintitle{text-align: left;}
    .sectionpadding{padding: 50px 0;}
    .sectionpaddingbottom{padding-bottom: 30px;}
    .clientimage{margin-bottom: 15px;}
    .clientheading h2{font-size: 30px; margin: 20px 0;float: left; text-align: left;}
    .clientheading h2::before{left:0px ;}
    .slider{padding: 50px 0;}
    
}
@media only screen and (max-width: 1199px){
    .subHeading h3::before{left: 380px;top: -5px;}
    .ourspeciality {margin-top: 20px;}
    .ourspeciality h3::before {left: 378px;}
    .imageHover figure{width: 100%;}
    .subHeading ::before {left: 390px;}
    .ourhappyclient ::before{left: 50px; top: -11px;}
    .servicesimage figure,.whyusimage figure,.ideaimage figure{width: 100%;}
    .ideaimage figure,.serviceimage figure,.clientimage figure{width: 100%;}
    .infosection{min-height: 696px;}
    .advantagesinfo{margin-bottom: 40px;}
    .ideaimage figure ::after{top: -16px;right: -16px;}
    .whyusimage figure ::before{top: -16px;left: -16px;}
    .clientinfo{padding: 20px;}
    .whyuscontent{padding: 64px 0;}
    .galleryheading h2::before{left:415px; width: 80px;}
    .clientheading h2::before{left:338px;top: -8px;}
}

@media only screen and (max-width: 991px){
    .imageWrap {padding: 5px;}
    .imageHover .showNow a {color:#ffffff; text-decoration: none;}
    .imageHover .showNow{font-size: 15px; position: absolute;left: 80%;background-color: #252a65;width: 80px;height: 35px;text-align: center;display: flex;flex-wrap: wrap;justify-content: center;align-items: center;top: 90%;border-radius: 5%;opacity: 1;text-decoration: none;}
    .imageHover {height: 350px;}
    .imageHover figure{height: 80%; margin-bottom: 20px;}
    .imageHover .show {margin: 0 20px; text-align: start;}
    .logo{font-size: 30px;}
    .navigation-menu li{margin-right: 20px;}
    .advantagesinfo .infosection,.clientinfo{margin-bottom: 20px;}
    .advantagesinfo:last-child .infosection,.clientsection1:last-child .clientinfo{margin-bottom: 0;}
    .ideaimage figure::after,.whyusimage figure::before{display: none;}
    .clientinfo{padding: 20px;}
    .infosection{min-height: 300px;}
    .ideacontent{padding: 49px 0;}
    .whyuscontent h3{margin-bottom: 20px; margin-top: 20px;}
    .whyusimage{margin-top: 0;}
    .whyuscontent{padding: 30px 0;}
    .whyusimage figure{height: 450px;}
    .subHeading h3::before {left: 0px}
    .subHeading{text-align: left; float: left;}
    .ourhappyclient ::before{left: 0px; top: -10px;}
    .ourhappyclient h3{text-align: left; float: left;}
    .ourspeciality h3 {text-align: left; float: left;}
    .ourspeciality h3::before {left: 0;}
    .galleryheading h2 {text-align: left;}
    .galleryheading h2::before{left:0px; width: 60px;top: -8px;}
    .clientheading h2::before{left:0px ;}
    .clientheading h2{left:0px ;text-align: left;}
}