/*BANNER CSS START*/
.banner{position: relative;width: 100%;padding-top: 90px;min-height: 150px;padding-bottom: 100px;}
.banner figure {float: left;width: 100%;position: absolute;top: 0;left: 0;bottom: 0;right: 0;margin: auto;width: 100%;height: 100%;}
.banner figure img {width: 100%;height: 100%;object-fit: cover;}
.bannerContent{width: 100%;position: relative;text-align: center;padding: 90px 0;}
.bannerContent h1 {font-size: 40px;}
.bannerContent h1::before{display: none;}
/*BANNER CSS END*/

/*director desk css start*/
.paddingtop{padding-top: 30px;}
.directorSection .main-title h3{width: 636px;}
.directorSectionImage figure{width: 460px;height: 450px;display: inline-block; object-fit: cover; overflow: hidden;}
.directorSectionImage figure img{float: left;width: 100%;height: 100%;border-radius: 50%;object-fit: cover; }
.directorSectionContent h3{font-size: 35px;margin-bottom: 10px;line-height: 45px;}
.directorSectionContent{padding: 100px 0;}
/*director desk css end*/

/*client service start*/
.subHeading{margin-bottom: 30px;}
.subHeading h3 {font-size: 35px;}
.subHeading ::before{left: 495px;top: -10px;}
.latestArrival{margin-top: 30px;}
.imageHover{float: left;width: 100%;position: relative; height: 280px;}
.imageHover figure{width: 100%;height: 200px;float: left; object-fit: cover;overflow: hidden;}
.imageWrap {margin: 10px 0;}
.imageHover figure img{width: 100%;height: 100%;float: left; overflow: hidden; object-fit: cover;}
.imageWrap .imageHover:hover figure img{opacity: 0.8; transition: 1s; transform: scale(1.1) translate3d(0, 0, 0);}
.imageHover .showNow{font-size: 19px; position: absolute;left: 52%;background-color: #fff;width: 80px;height: 40px;text-align: center;display: flex;flex-wrap: wrap;justify-content: center;align-items: center;top: 40%;border-radius: 5%;opacity: 0;transform: translate(-50%, -50%);text-decoration: none;}
.imageHover .show{font-size: 23px;font-weight: 600; color:#252a65;opacity: 1; text-align: center; margin-top: 20px;}
.imageWrap .imageHover:hover .showNow{opacity: 1; transition: 1s;}

/*client service end*/

/* history section start */
.history h3{margin-bottom: 20px;font-size: 35px;}
.history h3::before{float: left; left: 490px;top: -8px;}
.ethic h3::before {left: 530px; width: 60px;top: -8px;}

@media only screen and (max-width:992px){
  .subHeading ::before{left: 380px;top: -10px;}
  .history h3::before{float: left; left: 370px;top: -8px;}
.ethic h3::before {left: 415px; width: 60px;top: -8px;}
}
@media only screen and (max-width:991px){
  .latestArrival h3::before{left: 390px;}
    
  .history h3::before{left: 350px;}

  
    .imageHover figure{width: 100%;}
    .directorSectionImage figure{width: 100%;}
  

    
    .imageWrap {padding: 5px;}
    .imageHover .showNow a {color:#ffffff; text-decoration: none;}
    .imageHover .showNow{font-size: 19px; position: absolute;left: 86%;background-color: #252a65;width: 80px;height: 40px;text-align: center;display: flex;flex-wrap: wrap;justify-content: center;align-items: center;top: 90%;border-radius: 5%;opacity: 1;text-decoration: none;}
    .imageHover {height: 350px;}
    .imageHover figure{height: 80%; margin-bottom: 20px;}
    .imageHover .show {margin: 0 20px; text-align: start;}

    .latestArrival h3::before{left: 0px;}
    .latestArrival h3{float: left; text-align: left;}
    .history h3{float: left; text-align: left;}
    .history h3::before{left: 0px;}
    .directorSectionImage figure{width: 450px;height: 450px;}
    .directorSectionContent{padding: 30px 0;}
}
    
  

  @media only screen and (max-width:767px){
    .bannerContent h1 {font-size: 20px;}
    .imageHover .show{font-size: 13px;}
    .imageHover .showNow{left: 80%; width: 80px; height: 35px; font-size: 15px;}
    .directorSectionImage figure{width: 260px;height: 250px;}
    
    .latestArrival h3::before{left: 0px;}
    .latestArrival h3{float: left; text-align: left;}
    .history h3{float: left; text-align: left;}
    .history h3::before{left: 0px;}
  }