.full-width-header,.full-width{float: left;width: 100%;}
.header{background-color: #fff !important;color: #000;position: fixed;z-index: 11111;width: 100%;padding: 10px 0; margin: 0;}
.box-shadow{-moz-box-shadow: 0px 0px 3px rgba(82, 81, 81, 0.6);-webkit-box-shadow: 0px 0px 3px rgba(59, 57, 57, 0.6);
  box-shadow: 0px 2px 4px rgba(81, 92, 104, 0.5);}

.cart-icon li:last-child{margin-right: 0;}
.cart-icon li i{font-size: 22px;font-weight: 600;font-style: normal;color: #fff;}
.width{width: 100%;padding: 10px 10px;}
.form-elenent{margin: 12px 0;}
.navbar-brand{outline: none;padding:0;width: 100px; margin-top: -30px !important;}
.navbar-brand {width: 240px;}
.navbar-brand img {width: 100px;}
.navbar-brand h4{float: right;width: auto; margin-top: -60px; font-size: 23px; font-weight: 500;}
.navbar-brand h4::after{display: none;}
.navbar-brand span{color: #184bd9;font-size: 23px;padding-right: 6px;}

.form-elenent .form-control{width: 400px;height: 40px;border: none;padding: 0 30px;font-size: 14px;}
.navigation-menu{flex-direction: row;float: right;}
.navigation-menu li{margin-right: 5px;}
.navigation-menu li:last-child{margin-right: 0;}
.navigation-menu li a{line-height: 47px;color: #000;font-size: 14.9px;padding: 0 9px; outline: none;background: #fff;}
.navigation-menu li a:hover{background: #5f67c7 !important; border-top-left-radius: 27px;border-bottom-right-radius: 27px; border-bottom-left-radius: 10px;border-top-right-radius:10px;}
.form-control:focus{box-shadow: none;}
.cart-icon li a:hover i{color: #dc3545;}
.cart-icon li a:hover{background-color: #fff;box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.5);-webkit-box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.5);-moz-box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.5);}
.navigation-menu li a,.cart-icon li a,.cart-icon li a:hover i,.see-more a{transition: all 0.4s ease-in-out;-webkit-transition: all 0.4s ease-in-out;-moz-transition: all 0.4s ease-in-out;-o-transition: all 0.4s ease-in-out;-ms-transition: all 0.4s ease-in-out;}
.navbar-toggle .icon-bar{background-color: #000; }
.logo{padding-top: 0;padding-bottom: 0;}
.dropdown ul li{margin-right: 0;}
.dropdown-menu{border: none;top: 10px !important;}
.dropdown ul li a {line-height: 40px;}
.login{float: left;width: 50%;max-width: 100%;}
.login ul{width: auto;margin-right: 10px;margin-top: 12px;position: relative;}
.login ul li{float: left;margin-right: 30px;}
.login ul li::after{position: absolute;
  content: '';
  background-color: rgb(255, 255, 255);
  width: 2px;
  height: 17px;
  padding-left: 0;
  margin-left: 15px;}
.login ul li:last-child::after{display: none;}
.login ul li a{float: left;width: 100%;font-size: 14px;line-height: 16px;color: #fefeff;}
.login ul li:last-child{margin-right: 0;}
.login form{float: left;position: relative;}
i{font-family: fontawesome;}
.login form i{position: absolute;top: 9px;left: 11px;padding: 0 10px;font-size: 12px;line-height: 22px;color: #5c5c5f;}
.login form input{border: none;box-shadow: none;padding: 12px 40px;width: 200px;border: 1px solid transparent;border-radius: 25px;height: 40px;font-size: 14px;}
.login form input:focus{border: 1px solid #ffffff;}
.top-header{padding: 0 20px;}
.nav li a {background: #fff !important; transition: 0.6s;}
/* responsive */
@media only screen and (max-width: 1199px){
  .container > .row{margin: 0;}
  .navigation-menu li a{font-size: 11px;padding: 0 8px;}
 
}

@media only screen and (max-width: 991px){
  .navbar-brand{outline: none;padding:0;width: 50px; margin-top: -20px !important;}
.navbar-brand {width: 170px;}
.navbar-brand img {width: 75px;}
.navbar-brand h4{float: right;width: auto; margin-top: -45px; font-size: 15px; font-weight: 500;}
.navbar-brand h4::after{display: none;}
.navbar-brand span{color: #184bd9;font-size: 15px;padding-right: 6px;}
  
  .navigation-menu li{margin-right: 20px;}

}

@media only screen and (max-width: 767px){

  .navigation-menu{float: left;width: 100%;padding: 0 20px;}
  .navigation-menu li{margin-right: 0;}
  .navigation-menu li a{font-size: 13px;padding: 0 8px; line-height: 35px;}
}