/* You can add global styles to this file, and also import other style files */
/*Reset css start*/
ul{list-style-type: none;padding: 0;margin: 0;text-decoration: none;}
a{text-decoration: none;}
i{font-style: normal;}
h1,h2,h3,h4,h5,h6,body,p,input,span,form,header,figure,label,strong,bold{margin: 0;font-weight: normal;}
*{box-sizing: border-box;-webkit-box-sizing: border-box;-o-box-sizing: border-box;-moz-box-sizing: border-box;}
img{max-width: 100%;width: 100%;}
body {font-family: "Signika Negative", sans-serif;}
::-webkit-input-placeholder {opacity: 1; } ::-moz-placeholder {opacity: 1; } :-ms-input-placeholder {opacity: 1; } :-moz-placeholder {opacity: 1;}
::-webkit-input-placeholder {color: #5c5c5f;} ::-moz-placeholder {color: #5c5c5f;} :-ms-input-placeholder {color: #5c5c5f;} :-moz-placeholder {color: #5c5c5f;}
::-webkit-textarea-placeholder{font-size: 18px;}
input[type="email"]::-webkit-input-placeholder{color: #5c5c5f;}
input[type=search]::-webkit-search-decoration,input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-results-button,input[type=search]::-webkit-search-results-decoration {display: none; }
button[type="submit"]::-webkit-input-placeholder{color: #5c5c5f;}
address{font-style: normal;}
select {-webkit-appearance: none; -moz-appearance: none; appearance: none;}
option {font-weight: normal; display: block; white-space: pre; min-height: 1.2em; padding: 0px 2px 1px; }
.btn-default.active.focus, .btn-default.active:focus, .btn-default.active:hover, .btn-default:active.focus, .btn-default:active:focus, .btn-default:active:hover, .open>.dropdown-toggle.btn-default.focus, .open>.dropdown-toggle.btn-default:focus, .open>.dropdown-toggle.btn-default:hover,.btn-default:hover{background-color: #fff !important;}
.btn.active.focus, .btn.active:focus, .btn.focus, .btn:active.focus, .btn:active:focus, .btn:focus{outline: transparent;box-shadow: none;}
/*Reset css end*/

/* common css start */
    .full-width{float: left;width: 100%;}
    h1,h2,h3,h4,h5,h6,p{float: left;width: 100%;font-family: "Signika Negative", sans-serif;}
    a,address{font-family: "Signika Negative", sans-serif;}
    .center{text-align: center;}
    i{font-family: fontawesome;}
    .margin-bottom{margin-bottom: 50px;}
    .footer li a,.footer li address,.reach-us,.service-section:hover .services-image,.services-image figure,.services-image figure img,.service-section:hover .services-image,.service-section:hover .border-content,.services-image,.border-content,.border-content .border,.box-icon,.service-section:hover .border-content,.inner-content,.box-icon span,.box-icon span i,.service-section,.service-section:hover{transition: all 0.4s ease-in-out;-webkit-transition: all 0.4s ease-in-out;-moz-transition: all 0.4s ease-in-out;-o-transition: all 0.4s ease-in-out;-ms-transition: all 0.4s ease-in-out;}
    ::selection,-moz-selection,-webkit-selection,-ms-selection,-moz-selection,-o-selection {color: #fff; background: #0757a2;}
    .section-padding{padding: 80px 0;}
    .section-padding-bottom{padding-bottom: 60px;}
    .margin-top{margin-top: 50px;float: left;width: 100%;}
    .text-shadow{text-shadow: 8px 3px 12px #252a65;}
    h1{font-family: "Signika Negative", sans-serif;font-size: 65px;line-height: 70px;color: #fff;font-weight: 900;}
    h2{font-size: 63px;line-height: 49px;font-family: "Signika Negative", sans-serif;font-weight: 900;float: left;width: 100%;color: #252a65;}
    h3{float: left;width: 100%;font-family: "Signika Negative", sans-serif;font-weight: 900;font-size: 45px;line-height: 60px;color: #252a65;}
    h4{float: left;width: 100%;font-family: "Signika Negative", sans-serif;font-weight: 900;font-size: 19px;line-height: 24px;margin-bottom: 15px;color: #252a65;}
    h5{float: left;width: 100%;font-family: "Signika Negative", sans-serif;font-weight: 900;font-size: 18px;line-height: 20px;margin-bottom: 10px;color: #252a65;}
    h6{float: left;width: 100%;font-family: "Signika Negative", sans-serif;font-weight: 900;font-size: 16px;line-height: 18px;margin-bottom: 10px;color: #252a65;}
    p{float: left;width: 100%;font-size: 17px;line-height: 25px;}
    h2,h3,h4{position: relative;}
    h2::before,h3::before{position: absolute;content: '';display: inline-block;width: 100px;height: 1px;border-bottom: 4px solid #252a65;left: 0;top: -15px;}
    h4::after{position: absolute;content: '';display: inline-block;border: 2px solid #252a65;bottom: -10px;width: 50px;left: 0;}
    .main-title{margin-bottom: 30px;}
    .center{text-align: center;}
    .main-title h3{width: 276px;float: none;display: inline-block;}
    .box-shadow{box-shadow: 0px 0px 18px rgba(0,0,0,0.5)}
/* common css end */

/* slider css end*/
.owl-theme .owl-prev span , .owl-next span {font-size: 40px; margin: 0 10px;}
.owl-carousel.owl-loaded{padding: 40px 0 0 0;}
/* slider css start */

/* table css */
.editpassbtn {float:right; width: 23%;padding: 20px 0;}
.editpassform form {width: 70%;padding-top: 30px;}
.submit-btn{margin-top: 20px;}
.submit-btn .btn{font-size: 15px;padding: 10px 20px;line-height: 25px;border: none;border-radius: 25px;background-color: #252a65
    ;transition: all .4s;color: #fff;}
.submit-btn .btn:hover,.submit-btn .btn:focus{background-color: #252a65c7;
      box-shadow: 0 10px 29px 2px #252a65b6;;transition: all .4s;}

    
.editpassform form input{position: relative;width: 100%;padding: 10px; background: #f5f5f5;color:rgb(24, 23, 23);border: none;outline: none;box-shadow: none;margin: 8px 0;font-size: none;margin: 8px 0;font-size: 14px;letter-spacing: 1px;font-weight: 500;}
.editpassform form input[type='submit']{max-width: 100px; background: #252a65;color: #fff;cursor: pointer;font-size: 14px; font-weight: 500px;letter-spacing: 1px; transition: 0.5s;}
.MuiInputBase-input{font-size: 16px !important;}
.MuiTablePagination-caption{flex-shrink: 1 !important;}
.MUIDataTableHeadCell-fixedHeader-50{font-size: 17px !important;font-family: inherit !important; color:red !important;}
.MuiTypography-h6 {font-size: 2rem !important;font-family: inherit !important;font-weight: 700 !important;line-height: 0 !important;letter-spacing: 0.001em !important;}
.MuiTypography-body1 {font-size: 1.4rem !important;font-family: inherit !important;line-height: 0 !important;letter-spacing: 0.00938em !important;}
.MuiSelect-icon{font-size: 20px !important;}
.MuiMenuItem-root{font-size: 1.3rem !important;}
.MuiSelect-select.MuiSelect-select{font-size: 1.5rem !important; line-height: 1 !important; padding-right: 27px !important;}
.MuiTableCell-body{font-size: 14px !important; line-height: 1.3!important;}
.MuiSvgIcon-root{font-size: 2.5rem !important;}
.MuiTypography-body2{font-size: 1.12rem !important; font-family:"Signika Negative", sans-serif !important;}
.MUIDataTableSearch-searchText-89{top: 10px !important;}
.MuiInputLabel-formControl{font-size: 20px !important; font-family: inherit !important;}
.MuiTableCell-head{font-size: 13px !important; font-family: 'Signika Negative' , sans-serif !important; color: rgb(235, 29, 29) !important; }

/* toast notification css start*/
.Toastify__toast-container--top-right {top: 6.5em;right: 1.5em;}
/* toast notification css end */

/* client section common css start */
.clientsection .col-lg-3 {width: 20% !important;}
/* client section common css end */

/* team css start */
.team-section{box-sizing: border-box;padding-bottom: 15px;}
.containerteam {width:85%;margin: auto;}
.rowteam{display: flex;flex-wrap: wrap;}
.team-section .section-title{flex-basis: 100%;max-width: 100%;margin-bottom: 35px;}
.team-section .section-title h2::before{left: 470px;top: -8px;}
.team-section .section-title h2{font-size: 35px;text-align: center;font-weight: 700;}
.team-section .team-items{flex-basis: 100%;max-width: 100%;display: flex;flex-wrap:nowrap;justify-content: space-around;}
.team-section .team-items .item{flex-basis: calc(25% - 30px);max-width: calc(25% - 30px);transition: all .5s ease;margin-bottom: 40px;padding: 0 7px;}

.team-section .team-items .item img{ width: 100%;border-radius: 8px;height: 250px;}
.team-section .team-items .item .inner{position: relative;z-index: 11;padding:0 8px;}
.team-section .team-items .item .inner .info{background-color:#252a65;text-align: center;padding: 10px 10px;border-radius:8px;transition: all .5s ease;margin-top: -60px;height: 120px;}
.team-section .team-items .item:hover .info{transform: translateY(-20px);}
.team-section .team-items .item:hover{transform: translateY(-10px);}
.team-section .team-items .item .inner .info h5{margin:0;font-size: 15px;font-weight: 600;color:#ffffff;}
.team-section .team-items .item .inner .info p{font-size: 13px;font-weight: 500;color:#ffffff;margin:10px 0 0;}
/* team css end */

/*common  modal css start */
.modal {display: none; /* Hidden by default */position: fixed; /* Stay in place */z-index: 11111111; /* Sit on top */padding-top: 100px; /* Location of the box */left: 0;top: 0;width: 100%; /* Full width */height: 100%; /* Full height */overflow: auto; /* Enable scroll if needed */background-color: rgb(0,0,0); /* Fallback color */background-color: rgba(0,0,0,0.4); /* Black w/ opacity */}
.modal-content {background-color: #fefefe;margin: auto;padding: 40px;border: 1px solid #888;width: 80%;height: 320px;}
.arce {height: 600px;}
.close2,.close,.close3,.close4,.close5,.close6,.close7,.close8,.close9,.close10,.close11,.close12,.close13,.close14,.close15,.close16,.close17,.close18 {color: #2e2b2b;float: right;font-size: 28px;font-weight: bold;}
.close:hover,.close2:hover,.close:focus,.close2:focus,.close3:hover,.close3:focus,.close4:hover,.close5:hover,.close6:hover,.close7:hover,.close8:hover,.close9:hover,.close10:hover,.close11:hover,.close12:hover,.close13:hover,.close14:hover,.close15:hover ,.close16:hover,.close17:hover,.close18:hover{color: #000;text-decoration: none;cursor: pointer;}
/*common model css end  */

/* video player react css start */
.owl-carousel.owl-loaded {padding: 0;}
.player-wrapper {position: relative;padding-top: 81% /* Player ratio: 100 / (1280 / 720) */}
 
.react-player {position: absolute;top: 0;left: 0;}
/* video player react css end */
@media only screen and (max-width: 1199px){
  .clientsection .col-lg-3 {width: 33% !important;}
  .team-section .section-title h2::before{left: 300px;top: -8px;}
}
  @media only screen and (max-width: 911px){
  .tablepadding{padding-top: 100px;}
  .modal-content p{font-size: 13px;}
  .modal-content {height: 700px;}
  .arce {height: 1400px;}
  .clientsection .col-lg-3 {width: 50% !important;}
  .team-section .team-items{flex-wrap: wrap;}
  .MuiTypography-body1{font-size: 1.5rem !important;}
  .MuiTypography-body2{font-size: 1.4rem  !important;}
  .team-section .team-items .item{flex-basis: calc(50% - 30px);max-width: calc(50% - 30px);}
  .team-section .section-title h2::before{left: 0px;}
  .team-section .section-title h2{font-size: 30px;text-align: start;margin-bottom:6px;font-weight: 700;}
}
@media only screen and (max-width: 767px){
  .MuiTypography-root{margin: 5px !important;}
  .editpassbtn{width: 50%;}
  .containerteam {width:95%;margin: auto;}
  .team-section .team-items .item figure {width: 100%; height: 400px;}
  .team-section .team-items .item img {height: 90%;}
  .team-section .team-items .item{flex-basis: calc(100%);max-width: calc(100%);margin-bottom: 20px;}
  .team-section .team-items .item .inner .info{margin-top: -110px;}
  .imgarea img{display: inline-block;}
  .section-padding-bottom{padding-bottom: 30px;}
  .MUIDataTableToolbar-titleText-20{padding:20px 0 !important;}
  .owl-theme .owl-prev span , .owl-next span {font-size: 20px; margin: 0 5px;}
  .owl-carousel.owl-loaded{padding: 80px 0 0 0;}}





